@import "../Abstract/Variables.scss";

:root {
  --progress-accent-color: $headingBackgroundColor;
}

.skills {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  top: 70px;
  padding: 20px 0;
  width: 100% !important;

  @media (max-width: 1075px) {
    top: 115px !important;
    display: block;
  }

  &-heading {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 40px;
    text-align: center;

    h4 {
      background-color: $headingBackgroundColor;
      font-size: 18px;
      font-weight: 400;
      margin-bottom: 10px;
      border-radius: 5px;
      padding: 10px;
      width: 120px;
    }

    h2 {
      font-size: 36px;
      font-weight: 700;

      @media (max-width: 760px) {
        font-size: 24px !important;
      }

      @media (max-width: 480px) {
        font-size: 20px !important;
      }
    }
  }



    
    &-progress {
         width: 100%;
         display: flex;
         flex-direction: column;
         flex-wrap: nowrap;
         align-content: stretch;
         justify-content: center;
         align-items: center;

         @media (max-width: 768px) {
          width: 90%;
          gap: 15px;
        }
    
        @media (max-width: 480px) {
          width: 95%;
          gap: 10px;
        }
         .progress-1 {
           width: 80%;
           display: flex;
           flex-direction: column;
           margin-top: 30px;

           .html {
             display: flex;
             flex-direction: row;
             flex-wrap: wrap;
             align-content: center;
             justify-content: space-between;
             align-items: center;
           }
         }
       }

       progress[value] {
         width: 100%;
         height: 20px;
         accent-color: var(--progress-accent-color);
       }
 

}
